<style  lang="less" scoped>
.home {
  margin-bottom: 40px;
}
.data-view {
  box-shadow: 0 8px 16px 0 rgb(10 14 29 / 2%), 0 8px 40px 0 rgb(10 14 29 / 6%);
  margin-bottom: 15px;
  box-sizing: border-box;
  width: 100%;
  margin: auto;
  margin-bottom: 10px;
  overflow: hidden;
  background-color: #fff;
}
.li {
  overflow: hidden;
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
  position: relative;
  color: #373b3f;
  border-radius: 4px;
  box-sizing: border-box;
  margin: auto;
  .buy_size {
    font-size: 12px;
    color: #939ea9;
    font-weight: 300;
  }
  .size,
  .li-upx,
  .total {
    font-size: 14px;
    color: #939ea9;
  }
  .li-price {
    margin-bottom: 5px;
    color: #555;
    font-weight: 500;
  }
  .ratio {
    margin-bottom: 5px;
    color: #008555;
    font-weight: 700;
    span {
      font-size: 12px;
    }
  }
  .li-left {
    float: left;
    width: 120px;
    .name {
      font-weight: 700;
      font-size: 16px;
      margin-bottom: 5px;
      background-repeat: no-repeat;
      background-size: auto 16px;
      background-position: left center;
    }
    .size {
    }
  }
  .li-right {
    margin-left: 120px;
    overflow: hidden;
    text-align: right;
    .price-box {
      width: 50%;
      float: left;
    }
    .up-box {
      width: 50%;
      float: left;
    }
  }
}
.li::after {
  position: absolute;
  box-sizing: border-box;
  content: " ";
  pointer-events: none;
  right: 0;
  bottom: 0;
  left: 32px;
  border-bottom: 1px solid #ebedf0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.red {
  color: #d60f2a !important;
}
</style>
<template>
  <div class="home">
    <div class="data-main">

      <div class="data-view">
        <div id="main" style="width: 100%;height:220px;"></div>
        <van-cell-group>
          <van-cell is-link title="账户" :value="actions[actionsHover].name" @click="show = true" />
          <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
          <!-- <van-cell center title="人民币">
            <template #right-icon>
              <van-switch v-model="checked" size="24" />
            </template>
          </van-cell> -->
          <van-cell v-show="!checked" title="合计" :value="'$'+moneyFormat(rdata.total)" />
          <!-- <van-cell v-show="checked" title="合计" :value="'¥'+moneyFormat(rdata.total*6.6)" /> -->
        </van-cell-group>
      </div>
      <van-pull-refresh v-model="loading" @refresh="init">
        <div class="li" v-for="(item,index) in rdata.alist" :key="index" @click="actionBarShow=!actionBarShow">
          <div class="li-left">
            <div class="name">
              {{item.symbol}}<span class="buy_size"> | {{item.buy_size}}枚</span>
            </div>
            <div class="size">{{item.total_usdt}}</div>
          </div>
          <div class="li-right">
            <div class="price-box">
              <div class="li-price">{{item.price}}</div>
              <div class="li-upx">{{item.buy_price}}</div>
            </div>
            <div class="up-box">
              <div class="ratio" :class="{'red':item.profitRatio < 0}">{{item.profitRatio}}<span>%</span> </div>
              <div class="total">{{item.profit}}</div>
            </div>
          </div>
        </div>
      </van-pull-refresh>
      <van-action-sheet v-model="actionBarShow" description="添加交易" :actions="actionBar" cancel-text="取消" @select="onSelect" close-on-click-action />
    </div>
  </div>
</template>
<script>
var that;
var myChart;
var pricesWs;
import * as echarts from "echarts";
import { POST } from "../plugins/core";
export default {
  name: "index",
  data() {
    return {
      actionBarShow: false,
      actionBar: [{ name: "购买", color: "#ee0a24" }, { name: "出售" }],
      show: false,
      checked: false,
      actionsHover: 0,
      datacollection: [],
      actions: [
        { name: "账户一", index: 0, uid: 1 },
        { name: "账户二", index: 1, uid: 2 },
        { name: "账户三", index: 2, uid: 3 },
      ],
      loading: false,
      rdata: {
        total: 0,
        alist: [],
      },
    };
  },
  mounted() {
    that = this;
    that.init();
    myChart = echarts.init(document.getElementById("main"));
  },
  methods: {
    onSelect(item) {
      that.show = false;
      that.actionsHover = item.index;
      that.init();
    },
    moneyFormat(value) {
      // 金额 格式化
      if (!value && value !== 0) return "-";
      var intPart = Number(value) | 0; //获取整数部分
      var intPartFormat = intPart
        .toString()
        .replace(/(\d)(?=(?:\d{3})+$)/g, "$1,"); //将整数部分逢三一断

      var floatPart = ".00"; //预定义小数部分
      var value2Array = value.toString().split(".");

      //=2表示数据有小数位
      if (value2Array.length == 2) {
        floatPart = value2Array[1].toString(); //拿到小数部分

        if (floatPart.length == 1) {
          //补0,实际上用不着
          return intPartFormat + "." + floatPart + "0";
        } else {
          return intPartFormat + "." + floatPart;
        }
      } else {
        return intPartFormat + floatPart;
      }
    },
    chartInit() {
      let data = [];
      let list = that.rdata.alist;
      for (let i in list) {
        data.push({
          value: list[i].proportion,
          name: list[i].symbol,
        });
      }
      myChart.setOption({
        tooltip: {
          trigger: "item",
        },

        series: [
          {
            name: "资金占比",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              formatter: "{b} \n {c}%",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "18",
              },
            },
            labelLine: {
              show: true,
            },
            data: data,
          },
        ],
      });
    },
    init() {
      that.loading = true;
      let uid = that.actions[that.actionsHover].uid;
      POST("home", { uid }).then(function (res) {
        that.loading = false;
        that.rdata = res.data;
        let jsonCode = [];
        for (let i in that.rdata.alist) jsonCode.push(that.rdata.alist[i].name);
        let stringCode = jsonCode.join(",");
        if (pricesWs) pricesWs.close();
        pricesWs = new WebSocket(
          `wss://ws.coincap.io/prices?assets=${stringCode}`
        );
        pricesWs.onmessage = function (msg) {
          let rd = JSON.parse(msg.data);
          console.log(rd);
          for (let i in that.rdata.alist) {
            let price = rd[that.rdata.alist[i].name];
            let buy_price = Number(that.rdata.alist[i].buy_price);
            let buy_size = Number(that.rdata.alist[i].buy_size);
            if (price) {
              that.rdata.alist[i].price = price;
              that.rdata.alist[i].profit = Number(
                ((price - buy_price) * buy_size).toFixed(2)
              );
              that.rdata.alist[i].profitRatio = Number(
                (((price - buy_price) / buy_price) * 100).toFixed(2)
              );
            }
          }
        };
        that.chartInit();
      });
    },
  },
};
</script>
